.Login_Page_App {
  text-align: center;
}

.Login_Page_App-logo {
  -webkit-animation: Login_Page_A.Login_Page_App-logo-spin infinite 20s linear;
          animation: Login_Page_A.Login_Page_App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Login_Page_App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Login_Page_App-link {
  color: #61dafb;
}

@-webkit-keyframes Login_Page_A.Login_Page_App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Login_Page_A.Login_Page_App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

